import React, { useEffect, useState } from 'react';

function App() {

  const [meuArray, setMeuArray] = useState([]);

  async function insert(newArr){
    console.log(meuArray)
    await setMeuArray({
      ...setMeuArray,
      newArr
    });
  }

  useEffect(() => {
    window.addEventListener('@atlas/vue-todo-list', () => {
      var newArr = JSON.parse(localStorage.getItem('@atlas/vue-todo-list'));
      insert(newArr);
    })
  },[])
  
  return (
    <div>
      <ul>
        {meuArray.map(a => {
          return (
            <li style={{listStyle: 'none'}}>
              <span style={{margin: '12px 0'}} >{a.text}</span>
            </li>
          )
        })}
      </ul>
    </div>
  )
}

export default App;